import React, { useState } from "react"; 
import { getAuth, createUserWithEmailAndPassword, signInWithEmailAndPassword, GoogleAuthProvider, GithubAuthProvider, signInWithPopup } from 'firebase/auth';


const AuthForm = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [newAccount, setNewAccount] = useState(true);
  const [error, setError] = useState("");

  //onChange   input    . value  
  const onChange = (event) => {
    // event  {name, value} . => 
    // target :   , target  name(input ) value(   ) .
    const {target : {name, value}} = event;

    if(name === "email") {
      //name email  value={email} setemail 
      setEmail(value)
    } else if (name === "password") {
      //name password  value={password} setPassword 
      setPassword(value);
    }
  }

  const onSubmit = async (event) => {
    event.preventDefault();
    try {
      let data
      const auth = getAuth()
      if(newAccount) {
        data = await createUserWithEmailAndPassword(auth, email, password);
        } else {
        data = await signInWithEmailAndPassword(auth, email, password);
        }
    console.log (data);
  } catch(error) {
      setError(error.message);
  }
  };

  const toggleAccount = () => setNewAccount(prev => !prev)
  

  return (
  <>
  <form onSubmit={onSubmit} className="container">

<input 
name="email"
type="email" 
placeholder=" ."  
value={email}
onChange={onChange}
required 
/>

<input 
name="password"
type="password" 
placeholder=" ."
value={password}
onChange={onChange}  
required />

<input className="loginbtn" type="submit" value={newAccount ? "  " : ""}/>
{error}
</form>
<span className="loginspan" onClick={toggleAccount}>{newAccount ? "" : "  "}</span>
  </>
  )}

  export default AuthForm