import React, { useState } from "react";
import { dbService, storageService } from "fbase";
import { doc, deleteDoc, updateDoc }from"firebase/firestore";
import { deleteObject, ref } from "@firebase/storage";

//isOwner  props true & false   .
const Nweet = ({nweetObj, isOwner, userName}) => {

  //editing  nweet    .
  const [editing, setEditing] = useState(false);
  const [newNweet, setNewNweet] = useState(nweetObj.text);

  const onDeleteClick = async () => {
    // user  nweet   . 
    const ok = window.confirm(" nweet ?");
    if(ok) {
      await deleteDoc(doc(dbService, "nweets", `${nweetObj.id}`));
    nweetObj.previewUrl &&
    (await deleteObject(ref(storageService, nweetObj.previewUrl)));
    }
  }

const toggleEditing = () =>  setEditing((prev) => !prev);

const onSubmit= async (event) => {
  event.preventDefault();
  const NweetTextRef = doc(dbService, "nweets", `${nweetObj.id}`);
  await updateDoc(NweetTextRef, {
    text: newNweet,
    });
    setEditing(false);
}

const onChange = (event) => {
  const {
    target : {value},
  } = event;
  setNewNweet(value);
};

  return (
  <div>
    {
      editing ? (
      <div className="textwarp">
        <div className="textcontent">
        <form onSubmit={onSubmit}  >
           <textarea
            className="correctioninput"
              type="text" 
              placeholder="  ." 
              value={newNweet} 
              onChange={onChange}
              required 
          />
          <div className="setting">
          <input type="submit" value="" className="complete"/>
          <button onClick={toggleEditing} className="cencebtn"></button>
          </div>
          </form>
          </div>
      </div>
      ) :(
      <div className="textwarp">
        <div className="textcontent">
        {nweetObj.attachmentUrl && <img className="contentimg" src={nweetObj.attachmentUrl} />}
        <p className="todaytitle">[   ]</p>
        <h4 className="text">{nweetObj.text}</h4>
      {isOwner && (
      <div className="setting">
        <button className="correction" onClick={toggleEditing}></button>
        <button className="delete" onClick={onDeleteClick}></button> 
     </div>
    )}
    </div>
    </div>
      )
    }
  </div>
)
} 

export default Nweet;